<template>
  <div class="goods-detail">
    <div class="empty" v-if="!showTable">
      <h3>请在左侧选择一个仓库库位</h3>
    </div>
    <div class="content" v-else>
      <nav-button>
        <el-button-group>
          <el-button icon="el-icon-menu" :disabled="!isButtonDisabled()" @click="qrCodePrint">二维码打印</el-button>
          <el-button icon="el-icon-sort" :disabled="!isButtonDisabled()" @click="moveShelve">移库</el-button>
          <el-button icon="el-icon-download" @click="exportExcel">导出清单</el-button>
        </el-button-group>
      </nav-button>
          <common-table :flex="240" class="goods-table">
            <template slot="table">
              <el-table
                ref="goodsTable"
                height="100%"
                stripe
                :border="true"
                tooltip-effect="light"
                v-loading="isListLoading"
                :data="tableData"
                @selection-change="selected => selectRow = selected">
                <el-table-column type="selection" width="30" ></el-table-column>
                <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
                <el-table-column sortable prop="productMerchantName" label="商户" min-width="140"></el-table-column>
                <el-table-column sortable prop="productBrand" label="品牌" min-width="140"></el-table-column>
                <el-table-column sortable prop="productSeq" label="条码" min-width="160"></el-table-column>
                <el-table-column sortable prop="productName" label="货品名称" min-width="140"></el-table-column>
                <el-table-column sortable prop="productModel" label="型号" min-width="140"></el-table-column>
                <el-table-column sortable prop="productItemName" label="子件名" min-width="140" :show-overflow-tooltip="true"></el-table-column>
                <el-table-column sortable prop="productType" label="货品类型" min-width="140" :formatter="typeFormat"></el-table-column>
                <el-table-column sortable prop="productColor" label="颜色" min-width="140" :show-tooltip-when-overflow="true"></el-table-column>
                <el-table-column sortable prop="productStandard" label="规格" min-width="140" :show-tooltip-when-overflow="true"></el-table-column>
                <el-table-column sortable prop="productId" label="货品编号" min-width="160"></el-table-column>
                <el-table-column sortable prop="storageName" label="库位" min-width="120"></el-table-column>
                <el-table-column sortable prop="upgroundDate" label="上架时间" min-width="150" :formatter="formatDate"></el-table-column>
                <el-table-column prop="productStatus" label="货品状态" :formatter="statusFormat"></el-table-column>
                <el-table-column prop="receiveQualityStatus" label="入库质检" :formatter="receiveQualityFormat"></el-table-column>
                <el-table-column prop="sendQualityStatus" label="出库质检" :formatter="sendQualityFormat"></el-table-column>
                 <el-table-column prop="remark" label="备注" min-width="140" :show-overflow-tooltip="true"></el-table-column>
              </el-table>
            </template>
            <template slot="pagination">
              <el-pagination   background   layout="total, prev, pager, next"
                               :current-page.sync="pagination.pageNum"
                               :page-size="pagination.pageSize"
                               :total="pagination.total"
                               @current-change="changePage"
              ></el-pagination>
            </template>
          </common-table>
    </div>
    <!-- 移库 -->
    <move-shelve
      :show.sync="isMoveFormVisible"
      :selectRow="selectRow"
      @updateList="updateList"
    />
    <!-- 二维码打印 -->
    <qr-code
      :show.sync="qrCodeVisible"
      :tableData="selectRow"
    />
  </div>
</template>

<script>
import moment from 'moment'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import NavButton from '@/components/navButton'

import MoveShelve from './MoveShelve'
import QrCode from './QrCode'

import { setSession, getSession } from '@/utils/session'
import { getGoodsList, goodsExport } from '@/api/warehouse'
export default {
  name: 'GoodsDetail',
  data () {
    return {
      tableData: [],
      showTable: false,
      selectRow: [],
      searchParams: {},
      isListLoading: false, // 列表加载
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 15,
        total: 0
      },
      isMoveFormVisible: false,
      qrCodeVisible: false
    }
  },
  props: {
    searchData: {
      type: Object
    },
    selectedId: {
      type: String
    }
  },
  watch: {

  },
  methods: {
    // 查询和初始化查询
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getGoods(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 获取货品列表
    getGoods (params) {
      this.isListLoading = true
      getGoodsList(params).then(res => {
        if(res.item.length == 0 && res.pageNum > 1){
          this.pagination.pageNum = 1
          params.pageNum = 1
          this.getGoods (params)
          return
        }
        this.tableData = res.item
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
        this.showTable = true
      })
    },
    receiveQualityFormat (row) {
      if (row.receiveQualityStatus != null) {
        switch (row.receiveQualityStatus) {
          case 0:
            return '待质检'
            break
          case 1:
            return '质检通过'
            break
          case 2:
            return '质检不通过'
            break
          default:
            return '未知'
        }
      }
    },
    sendQualityFormat (row) {
      if (row.sendQualityStatus != null) {
        switch (row.sendQualityStatus) {
          case 0:
            return '待质检'
            break
          case 1:
            return '质检通过'
            break
          case 2:
            return '质检不通过'
            break
          default:
            return '未知'
        }
      }
    },
    typeFormat (row) {
      if (row.productType) {
        switch (row.productType) {
          case '0':
            return '小件'
            break
          case '1':
            return '套件'
            break
          case '2':
            return '单品'
            break
          default:
            return '未知'
        }
      }
    },
    formatDate (row) {
      if (row.upgroundDate)
        {return moment(row.upgroundDate).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')}
    },
    statusFormat (row) {
      if (row.productStatus != null) {
        switch (row.productStatus) {
          case 1:
            return '待收货'
            break
          case 2:
            return '已收货'
            break
          case 3:
            return '已质检'
            break
          case 4:
            return '库内存储'
            break
          case 5:
            return '待质检'
            break
          case 6:
            return '预出库'
            break
          case 7:
            return '已备货'
            break
          case 8:
            return '已发货'
            break
          case 9:
            return '待发货'
            break
          case 10:
            return '待维修'
            break
          case 11:
            return '待下架'
            break
          default:
            return '未知'
        }
      }
    },
    isButtonDisabled () {
      return !!this.selectRow.length
    },
    qrCodePrint () {
      this.qrCodeVisible = true
    },
    moveShelve () {
      this.isMoveFormVisible = true
    },
    exportExcel () {
      let params = this.searchParams
        goodsExport(params).then((res) => { // 处理返回的文件流
        const content = res.data
        const blob = new Blob([content], {type: 'application/ms-excel'})
        const fileName = '物件列表' + moment(new Date()).format('YYYYMMDD') + '.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      })
    },
    updateList () {
      this.queryList(this.searchParams)
    }
  },
  components: {
    CommonWrapper,
    CommonTable,
    NavButton,
    MoveShelve,
    QrCode
  }
}
</script>

<style lang="scss" scoped>
  .goods-detail {
    position: relative;
    .empty{
      text-align: center;
      padding:30px;
      font-size:2rem;
      color: darken($base-light-color, 15%);
    }
    .content {
      width:100%;
      max-width:1200px;

    }
    .goods-table{
      position: absolute;
      width: 100%;
    }
  }
</style>



